import React, { Component, PureComponent, Suspense, lazy } from 'react';
import { connect } from 'react-redux';
import App from './app';
import { getThemeDataWithWallID, showPopUP } from '../../../../../../actions/themeActions/themeActions'
import TransitionEffects from './app/Components/Transitions'
import PlanLimit from './app/Components/ErrorPages/PlanLimit/PlanLimit';
import ErrorPage from "./app/Components/ErrorPages";
// import ModalPopUp from "./app/Components/PostPopUp";
import ErrorFound from './app/Components/errorFallback';
import { getWallID } from './utils'
import { componentWillAppendToBody } from "react-append-to-body";
import LoadingScreen from '../../../../../pages/LoadingScreen';
const ModalPopUp = lazy(() => import('./app/Components/PostPopUp'));

const MyComponent = ({ children }) => { return children;}
const AppendedMyComponent = componentWillAppendToBody(MyComponent);

const TaggboxThemes = (props) => {
  if (props.loader && props.loader.loader != null && props.error.errorWithMessage == null && !props.error.planLimit) return  <App {...props} /> 
  else if (props.error.planLimit) return <PlanLimit />
  else if (props.error.errorWithMessage != null) return <>ERROR</>
  else return null
}

class Route extends PureComponent {
  componentDidMount() {
   // const { wallID } = this.props; 
   // this.props.getThemeDataWithWallID(wallID, null) 

}

  render() {
    const { appData, modalPop } = this.props
    const error_code = appData && appData.error && appData.error.themeError ? appData.error.themeError.error_code : 0;
    const noData = appData && appData.postData &&  appData.postData.postData && appData.postData.postData.length==0  && !window.isEditor ? true : false; 
    
    return(<div className="taggshopContainer popupOpenInIt">
       { !noData ? 
                !error_code ? 
                  <div className="taggshopContainer1">
                        {appData?.wall?.Personalization?.commerceTheme ? <TaggboxThemes {...appData} />: <LoadingScreen/>}
                        {modalPop.isShowPopUp && <ErrorFound><Suspense fallback={null}> <ModalPopUp data={modalPop.data} /></Suspense></ErrorFound>}
                  </div>
                  : <ErrorPage wall={appData.wall} errorData={appData.error}/>
        : ''
        // <div > <h1><center> No Posts Found </center></h1></div>
        }
    </div>)

  }
}
const mapStateToProps = state => {
  return {
    appData: state.appData,
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return {
    //getThemeDataWithWallID: (Id, heightEvent) => dispatch(getThemeDataWithWallID(Id, heightEvent))
   
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Route);

